import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import Status from '../../../components/Status';
import style from './MoveFundsAnotherBankSuccess.module.css';
import { useAppSelector } from '../../../hooks/store';
import { useDrawer } from '../../../hooks/drawer';

function MoveFundsAnotherBankSuccess() {
  const { t } = useTranslation();
  const { close } = useDrawer();
  const paymentDetails = useAppSelector((store) => store.payment.paymentResponse, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);

  return (
    <div className="page">
      <MiniBox>
        <Status
          status="success"
          title={`${paymentDetails?.amount} Transferred`}
          ctaText={t('move_funds_success.ctaText')}
          onNext={() => {
            close();
          }}
        >
          <Box>
            <Text variant="regular" className={`${style.transfer_initiated} text-light`} pt="16px">{t('move_funds_success.info')}</Text>
            <Box className={style.card_info}>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('move_funds_success.source')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{account?.label}</Text>
              </HStack>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('move_funds_success.destination')}</Text>
                <Text w="50%" variant="regular" textAlign="left">
                  {`${paymentDetails?.bankName} XXXXXX ${paymentDetails?.accountNumber?.slice((paymentDetails?.accountNumber?.length || 4) - 4)}` }

                </Text>
              </HStack>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('move_funds_success.description')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.description}</Text>
              </HStack>
            </Box>
          </Box>
        </Status>
      </MiniBox>
    </div>
  );
}

export default MoveFundsAnotherBankSuccess;
